/* eslint-disable */
import React from "react"
import Navbar from "../partials/navbar"
import Divider from "../images/curvestrawberry.svg"
import RavenLogo from "../images/ravenhackfeather.svg"
import RavenHackLogo from "../images/ravenhacklogo.svg"
import Footer from "../partials/footer"
import Card, {Header} from "../partials/card"
import { Link } from "gatsby"

export default () => (<div>
  <Navbar pageName="Home" />
      <div style={{background: '#d53032', marginTop:"-1px"}}>
        <img src={RavenHackLogo} alt="Logo" id="logo"width="30%" style={{marginLeft: "35%"}}/>
      </div>

  <img src={Divider} width="100%" alt="Divider" style={{
    marginTop :"-1px"
  }}/>
  <div className="container">
  <h1 className="yellow-text important" style={{fontSize:50}}>Welcome to Raven Hack '20</h1>
    <Header content="Hey There ðŸ‘‹"></Header>
    <Card title="So what's Raven Hack all about?" content="Raven Hack is an attempt to unite the algorithm enthusiasts of Canyon Crest Academy under a unique and rare opportunity - in fact, this is the first hackathon at CCA, Carmel Valley, and one of the few in San Diego. So yeah, it's cool. Will it be a success? We hope so, but this hackathon is also about setting a precedent."></Card>
    <Header content="Frequently Asked Questions"></Header>
    <div className="row wow animated bounceInUp">
      <div className="col-sm-4" style={{marginBottom: "20px"}}>
      <Card className="yellow-gradient" content="Of course not! This event's primary goal is to invent an entertaining yet competitive experience for all, with programming simply being the medium." title="Do I need to know how to code?"/>
      </div>
      <div className="col-sm-4" style={{marginBottom: "20px"}}>
      <Card content="As much as we'd like to include anyone, currently, the event is only open for students at Canyon Crest Academy. Other than that, any student may attend." title="Who can attend?"/>
        
      </div>
      <div className="col-sm-4" style={{marginBottom: "20px"}}>
        <Card content="The hosting details are still being formalized, but it's looking like it'll be held in Canyon Crest Academy's Media Center from 12-6PM." title="Where is it?"/>
        </div>
    </div>
  </div>
  <Footer/>
</div>)
